import React from 'react';
import { useState } from 'react';
import './StrengthIndicator.css';

interface Props {
    score: number;
}

const StrengthIndicator = ( score: Props ) => {
    console.log(score.score);
    if(score.score === 0){
    return (
            <div className="zero">
            </div>
    );}
    if(score.score === 1){
    return (
            <div className="one">
            </div>
    );}
    if(score.score === 2){
        return (
                <div className="two">
                </div>
    );}
    if(score.score === 3){
        return (
                <div className="three">
                </div>
    );}
    if(score.score === 4){
            return (
                <div className="four">
                </div>
    );}
    else return <div></div>
}

export default StrengthIndicator;